    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        created(){
            this.dataDay = this.$route.params.definite;
        },
        mounted(){
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.getErrorWorkList,
                type: "post",
                data: {
                    date: this.dataDay
                },
                success: (response, allResponse) => {
                    console.log('response',response);
                    this.serveStaffList = response
                }
            });
        },
        data() {
            return {
                dataDay: null,
//                员工信息
                serveStaffList: [
                    { head_img: require("@/assets/images/head.jpg"),username:"陈小丽",job_number:"9527",status_name:"迟到2分钟",day:"08/17 9:55" },
                    { head_img: require("@/assets/images/head.jpg"),username:"陈一丽",job_number:"9528",status_name:"迟到2分钟",day:"08/17 9:55" },
                    { head_img: require("@/assets/images/head.jpg"),username:"陈二丽",job_number:"9520",status_name:"迟到2分钟",day:"08/17 9:55" },
                    { head_img: require("@/assets/images/head.jpg"),username:"陈三丽",job_number:"9529",status_name:"迟到2分钟",day:"08/17 9:55" }
                ],
            }
        }
    }



